*
  box-sizing: border-box
  outline: none
*:focus
  outline: none
textarea,
input
  -webkit-appearance: none
  border-radius: 0
ul[class],
ol[class]
  padding: 0

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd
  margin: 0

ul[class]
  list-style: none

img
  max-width: 100%
  display: block

input,
button,
textarea,
select
  font: inherit
  outline: none
a
  text-decoration: none
  color: inherit
button
  cursor: pointer
  border: none
  padding: 0
  background-color: transparent
